"use client";
import { useState } from "react";
import { Proveedor } from "../../dashboard/proovedor/page";
import { FiPlus, FiX } from "react-icons/fi";

interface Props {
  onAgregar: (proveedor: Omit<Proveedor, "idProveedor">) => void;
  onCancelar: () => void;
}

export default function ProveedorForm({ onAgregar, onCancelar }: Props) {
  const [formData, setFormData] = useState<Omit<Proveedor, "idProveedor">>({
    ruc: "",
    razonSocial: "",
    direccion: "",
    telefono: "",
    paginaWeb: "",
    email: "",
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!formData.ruc || !formData.razonSocial) return;
    onAgregar(formData);
  };

  return (
    <form className="formulario-empresa" onSubmit={handleSubmit}>
      <div className="campo-formulario">
        <label>N° RUC</label>
        <input name="ruc" placeholder="Ingrese RUC del Proveedor" value={formData.ruc} onChange={handleChange} required />
      </div>
      <div className="campo-formulario">
        <label>Razón Social</label>
        <input name="razonSocial" placeholder="Ingrese Razón Social del Proveedor" value={formData.razonSocial} onChange={handleChange} required />
      </div>
      <div className="campo-formulario">
        <label>Dirección</label>
        <input name="direccion" placeholder="Ingrese Dirección del Proveedor" value={formData.direccion} onChange={handleChange} />
      </div>
      <div className="campo-formulario">
        <label>Teléfono</label>
        <input name="telefono" placeholder="Ingrese Teléfono del Proveedor" value={formData.telefono} onChange={handleChange} />
      </div>
      <div className="campo-formulario">
        <label>Página Web</label>
        <input name="paginaWeb" placeholder="Ingrese Página Web del Proveedor" value={formData.paginaWeb} onChange={handleChange} />
      </div>
      <div className="campo-formulario">
        <label>E-Mail</label>
        <input name="email" placeholder="Ingrese Email del Proveedor" value={formData.email} onChange={handleChange} />
      </div>

      <div className="botones-formulario">
        <button type="submit" className="boton-form negro">
          <FiPlus /> Guardar
        </button>
        <button type="button" onClick={onCancelar} className="boton-form gris">
          <FiX /> Cerrar
        </button>
      </div>
    </form>
  );
}
